import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { AutoDestroy } from '@core/common/auto-destroy'
import { takeUntil } from 'rxjs'
import { Product } from '@core/models/product.model'

@Component({
  selector: 'app-promo-banner',
  templateUrl: './promo-banner.component.html',
  styleUrls: ['./promo-banner.component.scss']
})
export class PromoBannerComponent extends AutoDestroy implements OnInit {

  @Input()
  public categoryId?: number

  @Output()
  public addToCart = new EventEmitter<any>()

  public product: Product

  constructor(private api: ApiService) {
    super()
  }

  ngOnInit(): void {
    this.getProduct()
  }

  private getProduct() {
    this.api.get('product/promo-product/2')
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.product = data
      })
  }

  public _addToCart() {
    this.addToCart.emit()
  }
}
