import { Component, Inject, OnInit } from '@angular/core'
import { Form } from '@core/common/form2'
import { FormBuilder, Validators } from '@angular/forms'
import { BreakpointObserver } from '@angular/cdk/layout'
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog'
import { ApiService } from '@core/services/api.service'
import { takeUntil } from 'rxjs'

@Component({
  selector: 'app-add-review',
  templateUrl: './add-review.component.html',
  styleUrls: ['./add-review.component.scss']
})
export class AddReviewComponent extends Form implements OnInit {

  public isMobile = true
  public rating = 0

  private formFields = {
    email: ['', [Validators.required, Validators.email] ],
    name: ['', [Validators.required] ],
    message: ['', [] ],
    rating: [1],
  }

  constructor(
    private fb: FormBuilder,
    private breakpointObserver: BreakpointObserver,
    private dialogRef: MatDialogRef<AddReviewComponent>,
    private api: ApiService,
    @Inject(MAT_DIALOG_DATA) private data: {slug: string}
  ) {
    super()
    this._initFormControl(fb, this.formFields)
  }

  ngOnInit(): void {
    this.breakpointObserver
      .observe(['(min-width: 576px)'])
      .subscribe((state) => this.isMobile = !state.matches)
  }

  cancel() {
    this.dialogRef.close(null)
  }

  save() {
    this.form.markAllAsTouched()

    if (! this.form.valid) {
      return
    }

    const data: {[key: string]: string} = {}

    Object.entries(this.form.controls).forEach(([key, fc], i) => {
      data[key] = fc.value
    })

    this.api.post(`product/review/${this.data.slug}`, data)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.dialogRef.close(data)
      })

  }

}
