import { Component, OnInit } from '@angular/core'
import { Product } from '@core/models/product.model'
import { AutoDestroy } from '@core/common/auto-destroy'
import { ApiService } from '@core/services/api.service'
import { ActivatedRoute } from '@angular/router'
import { finalize, takeUntil } from 'rxjs'
import { BreakpointObserver, BreakpointState } from '@angular/cdk/layout'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { MatBottomSheet } from '@angular/material/bottom-sheet'
import { CartBottomSheetComponent } from '../../common/cart-bottom-sheet/cart-bottom-sheet.component'
import { CartDialogComponent } from '../../common/cart-dialog/cart-dialog.component'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { ProductCategory } from '@core/models/types'
import { NotificationService } from '@core/services/notification.service'
// import { CartPopupComponent } from '../../common/cart-popup/cart-popup.component'

@Component({
  selector: 'app-product-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent extends AutoDestroy implements OnInit {

  public product: Product
  public menuItems: { slug: string, title: string }[]
  public similarProducts: Array<Product>
  public recommendedProducts: Array<Product>
  public saleProducts: Array<Product>
  public loading = false

  public isMobile = true
  private isSmMobile = true

  constructor(
    private api: ApiService,
    public route: ActivatedRoute,
    private breakpointObserver: BreakpointObserver,
    private dialog: MatDialog,
    private bottomSheet: MatBottomSheet,
    private notifier: NotificationService
  ) {
    super()
  }

  ngOnInit(): void {
    const breakpoints = ['(min-width: 768px)', '(min-width: 576px)']
    this.breakpointObserver
      .observe(breakpoints)
      .pipe(takeUntil(this.destroy$))
      .subscribe((state: BreakpointState) => {
        this.isMobile = !state.breakpoints[breakpoints[0]]
        this.isSmMobile = !state.breakpoints[breakpoints[1]]
      })

    this.route.params
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.fetchProduct()
        this.fetchSimilarProducts()
        this.fetchRecommendedProducts()
        this.fetchSaleProducts()
      })

    // this.openCartPopup()
  }

  public addToCart(quantity: number|string = 0) {
    this.openCartPopup()
  }

  private openCartPopup() {
    if (this.isSmMobile) {
      this.bottomSheet.open(CartBottomSheetComponent)
    } else {
      this.dialog.open(CartDialogComponent, {
        width: '768px'
      })
    }
  }

  private fetchProduct() {
    this.api.get(`article?strId=${this.route.snapshot.params['randomProductId']}`, MicroserviceType.inv)
    // this.api.get(`product/${this.route.snapshot.params['randomProductId']}`)
      .pipe(takeUntil(this.destroy$))
      .subscribe((data) => {
        this.product = Product.fromDto(data)
        // this.createBreadcrumbs(this.product)
      })
  }

  private createBreadcrumbs(product: Product) {
    const menuItems = [{ slug: `${product.seo?.metaUrl}/${product.randomProductId}`, title: product.name }]
    let category: ProductCategory|undefined = product.primaryCategory
    while (category) {
      menuItems.push({ slug: `${category.friendlyUrl}/${category.randomCategoryId}`, title: category.name })
      category = category.parent
    }
    this.menuItems = menuItems.reverse()
  }

  private fetchSimilarProducts() {
    this.api.get('product/similar/q')
      .subscribe(({ data }) => {
        this.similarProducts = data.products
      })
  }

  private fetchRecommendedProducts() {
    this.api.get('product/recommended/a')
      .subscribe(({ data }) => {
        this.recommendedProducts = data.products
      })
  }

  private fetchSaleProducts() {
    this.api.get('product/sale/q')
      .subscribe(({ data }) => {
        this.saleProducts = data.products
      })
  }

}
