import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core'
import SwiperCore, { FreeMode, Navigation, Pagination, Thumbs } from 'swiper'
import { SwiperComponent as OrigSwiperComponent } from 'swiper/angular'
import { Product } from '@core/models/product.model'
import { BreakpointObserver } from '@angular/cdk/layout'
import Swiper from 'swiper'
import { NavigationOptions } from 'swiper/types'

SwiperCore.use([FreeMode, Navigation, Thumbs, Pagination])

@Component({
  selector: 'app-img-swiper',
  templateUrl: './img-swiper.component.html',
  styleUrls: ['./img-swiper.component.scss'],
})
export class ImgSwiperComponent implements OnInit {

  @ViewChild('swiper', { static: false }) swiper: OrigSwiperComponent

  @Input()
  public product: Product

  public navOpt: NavigationOptions = { nextEl: '.swiper-product-img-next', prevEl: '.swiper-product-img-prev', }
  public isMobile: boolean
  public thumbsSwiper: Swiper

  constructor(private breakpointObserver: BreakpointObserver) { }

  ngOnInit(): void {
    this.breakpointObserver
      .observe(['(max-width: 767px)'])
      .subscribe((state) => this.isMobile = state.matches)
  }

  slideNext() {
    this.swiper.swiperRef.slideNext(100)
  }
  slidePrev() {
    this.swiper.swiperRef.slidePrev(100)
  }

}
