import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core'
import { Product, StockTypeEnum, VariationAttributes, VariationAttributeValue } from '@core/models/product.model'
import { Router } from '@angular/router'
import { MicroserviceType } from '@core/enums/microservice-type.enum'
import { finalize } from 'rxjs'
import { ApiService } from '@core/services/api.service'
import { CartService } from '@core/services/cart.service'

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {

  @Input()
  public product: Product

  @Output()
  public addedToCart = new EventEmitter()

  quantity = 1
  availableQtys = Array.from({ length: 10 }, (_, i) => i + 1)
  public loading = { addToCart: false }
  public StockTypeEnum = StockTypeEnum

  constructor(private router: Router, private api: ApiService, private cart: CartService) {}

  ngOnInit() {
    this.availableQtys = Array.from({ length: this.product.stock ?? 10 }, (_, i) => i + 1)
  }

  addToCart() {
    this.loading['addToCart'] = true
    this.cart.addToCart(this.product, this.quantity)
      .pipe(finalize(() => this.loading['addToCart'] = false))
      .subscribe(() => this.addedToCart.emit())
  }

  gotoVariation(attr: VariationAttributeValue, attrName: keyof VariationAttributes) {
    const variation = this.findVariation(attr, attrName)

    if (variation) {
      this.router.navigate([`/product/${variation.seo?.metaUrl}/${variation.randomProductId}`])

    }
  }

  findVariation(attr: VariationAttributeValue, attrName: keyof VariationAttributes) {
    const variationNamesMap: Record<keyof VariationAttributes, keyof VariationAttributes> = { Size: 'Color', Color: 'Size' }

    if (! this.product.variationAttributes) {
      return null
    }

    const otherSelectedVariationAttrName: keyof VariationAttributes = variationNamesMap[attrName]

    const otherSelectedVariationAttrId = this.product.variationAttributes[otherSelectedVariationAttrName]?.variationValues[0].attributeValue.id

    return this.product.parent?.variations?.find((variation) => {

      console.log(variation?.variationAttributes?.[attrName])

      // check if pass attribute matches
      if (attr.attributeValue.id !== variation.variationAttributes?.[attrName].variationValues[0].attributeValue.id) {
        return false
      }

      // passed attr matches
      // now check if other matches
      if (otherSelectedVariationAttrId) {
        if (variation.variationAttributes[otherSelectedVariationAttrName].variationValues[0].attributeValue.id !== otherSelectedVariationAttrId) {
          return false
        }
      }

      // both attrs, size and color matches
      return true
    })
  }

  isVariationNotAvailable(attr: VariationAttributeValue, attrName: keyof VariationAttributes) {
    return !this.findVariation(attr, attrName)
  }
}
