import { Component, Input, OnInit } from '@angular/core'
import { ApiService } from '@core/services/api.service'
import { AutoDestroy } from '@core/common/auto-destroy'
import { takeUntil } from 'rxjs'
import { ProductReview } from '@core/models/product.model'
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog'
import { AddReviewComponent } from '../add-review/add-review.component'

@Component({
  selector: 'app-product-reviews',
  templateUrl: './product-reviews.component.html',
  styleUrls: ['./product-reviews.component.scss']
})
export class ProductReviewsComponent extends AutoDestroy implements OnInit {

  @Input()
  public slug: string

  public total = 0
  public perPage = 3
  public page = 1
  public rating = 0.0
  public reviews: Array<ProductReview> = []

  constructor(private api: ApiService, private dialog: MatDialog) {
    super()
  }

  ngOnInit(): void {
    this.fetchReviews()
  }

  private fetchReviews() {
    this.api.get('product/reviews/q', null, { page: this.page })
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ data }) => {
        this.reviews = data.reviews
        this.total = data.total
        this.rating = data.rating
      })
  }

  gotoPage(currPage: number) {
    this.page = currPage

    this.fetchReviews()
  }

  showAddReview() {
    const dialogRef = this.dialog.open(AddReviewComponent, {
      width: '788px',
      data: { id: this.slug }
    })

    dialogRef.beforeClosed()
      .pipe(takeUntil(this.destroy$))
      .subscribe(review => {
        if (review) {
          this.fetchReviews()
        }
      })

  }
}
